
.time-picker-modal-container {
	width:250px;
  position: absolute;
  opacity: 1;
  z-index: 2;
  padding: 25px;
  transition: all 400ms cubic-bezier(0.165, 0.84, 0.44, 1);
  background-color: #fff;
  border-radius: 2px;
  top: 100%;
  left: 0;
  transform: translateY(20px);
  box-shadow: 0 4px 4px 0 rgba(0, 0, 0, 0.12), 0 0 4px 0 rgba(0, 0, 0, 0.08);
}
.time-picker-modal-container:hover{
	box-shadow: 0 8px 8px 0 rgba(0, 0, 0, 0.12), 0 0 8px 0 rgba(0, 0, 0, 0.08);
}
.time-picker-modal-header {
  height: 50px;
  line-height: 20px;
  text-align: center;
  font-size: 2.5em;
  position: relative;
}
.time-picker-modal-header .time-picker-header {
  cursor: pointer;
}
.time-picker-modal-header .time-picker-header.active {
  color: #3498db;
  cursor: default;
}
.time-picker-modal-header .time-picker-header:hover {
  color: #3498db;
}
.picker-container {
  width: 250px;
  height: 250px;
  border-radius: 50%;
  background-color: #e5e5e5;
  position: relative;
}
.picker-center {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 10px;
  height: 10px;
  border-radius: 50%;
  background-color: #3498db;
  transform: translate(-50%, -50%);
}
.picker-pointer {
  position: absolute;
  width: 4px;
  height: 110px;
  left: 50%;
  top: 20px;
  background-color: #3498db;
  transform-origin: center bottom;
}
.picker-pointer.animation {
  transition: all 400ms cubic-bezier(0.165, 0.84, 0.44, 1);
}



